@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@400;500;600;700&display=swap');

/* --------- VARIABLES ---------- */
$color-one: #1f1f1f;
$color-two: #5f4128;
$color-three: #a24d06;
$color-bg: #edf1f4;
$border: #9e9e9e;
$color-text: #5a5a5a;
$color-black: #363636;

$max-width: 1200px;
$max-shop-width: 2500px;
$max-width-modal: 1000px;

$phone: 768px - 1;
$tablet: 1024px - 1;

$color-blue-1: rgb(25, 118, 210);
$color-blue-2: rgba(25, 118, 210, 0.7);

$box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1), -5px -5px 10px #fff;
$box-shadow-inset: inset 5px 5px 10px rgba(0, 0, 0, 0.1), inset -5px -5px 10px #fff;

/* ---------  ---------- */

/* --------- GENERAL STYLES ---------- */
* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

body {
   font-family: -apple-system, BlinkMacSystemFont, 'Poppins', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
      'Droid Sans', 'Helvetica Neue', 'Segoe UI', sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   background-color: $color-bg;
   color: $color-text;

   // предотвращение смещения элементов при появлении и исчезании вертикальной полосы прокрутки
   overflow-y: auto !important;
   padding-right: 0 !important;
}

h1 {
   font-family: 'Dancing Script', cursive;
   font-weight: 700;
   font-size: 4rem;
   color: $color-blue-1;
}

svg {
   vertical-align: unset;
}

// размер шрифта всплывающей подсказки
.MuiTooltip-popper .MuiTooltip-tooltip {
   font-size: 12px;
}

// размытие сзади модального окна
.MuiBackdrop-root {
   backdrop-filter: blur(2px);
}

.snackbar {
   margin-bottom: 20px;
}

fieldset {
   border: none !important;
}

.add-contacts .form-wrap .form fieldset {
   border: none !important;
}

@media only screen and (max-width: $phone) {
   .hideOnMobile {
      display: none;
   }
}

/* --------- HEADER ---------- */
.header {
   position: fixed;
   width: 100%;
   background-color: $color-bg;
   box-shadow: $box-shadow;
   z-index: 100;

   .container {
      display: flex;
      align-items: center;
      justify-content: space-between;
   }

   h1 {
      text-align: center;
      flex: 1;

      span {
         position: relative;

         svg {
            font-size: 2.3rem;
            vertical-align: baseline;
            color: inherit;
         }

         &::after {
            content: '';
            position: absolute;
            width: 3rem;
            height: 3rem;
            border-radius: 100%;
            border: 3px solid $color-blue-1;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
         }
      }
   }

   /* SWITH TOGGLE */
   .swith-toggle-theme {
      margin-left: auto;
      .label {
         display: -webkit-inline-box;
         display: -ms-inline-flexbox;
         display: inline-flex;
         -webkit-box-align: center;
         -ms-flex-align: center;
         align-items: center;
         cursor: pointer;
         color: #394a56;
      }

      .label-text {
         margin-left: 16px;
      }

      .toggle {
         isolation: isolate;
         position: relative;
         height: 30px;
         width: 60px;
         border-radius: 15px;
         overflow: hidden;
         -webkit-box-shadow: -8px -4px 8px 0px #ffffff, 8px 4px 12px 0px #d1d9e6, 4px 4px 4px 0px #d1d9e6 inset,
            -4px -4px 4px 0px #ffffff inset;
         box-shadow: -8px -4px 8px 0px #ffffff, 8px 4px 12px 0px #d1d9e6, 4px 4px 4px 0px #d1d9e6 inset,
            -4px -4px 4px 0px #ffffff inset;
      }

      .toggle-state {
         display: none;
      }

      .indicator {
         height: 100%;
         width: 200%;
         background: #ecf0f3;
         border-radius: 15px;
         -webkit-transform: translate3d(-75%, 0, 0);
         transform: translate3d(-75%, 0, 0);
         -webkit-transition: -webkit-transform 0.4s cubic-bezier(0.85, 0.05, 0.18, 1.35);
         transition: -webkit-transform 0.4s cubic-bezier(0.85, 0.05, 0.18, 1.35);
         transition: transform 0.4s cubic-bezier(0.85, 0.05, 0.18, 1.35);
         transition: transform 0.4s cubic-bezier(0.85, 0.05, 0.18, 1.35),
            -webkit-transform 0.4s cubic-bezier(0.85, 0.05, 0.18, 1.35);
         -webkit-box-shadow: -8px -4px 8px 0px #ffffff, 8px 4px 12px 0px #d1d9e6;
         box-shadow: -8px -4px 8px 0px #ffffff, 8px 4px 12px 0px #d1d9e6;
      }

      .toggle-state:checked ~ .indicator {
         -webkit-transform: translate3d(25%, 0, 0);
         transform: translate3d(25%, 0, 0);
      }
   }
}

/* --------- MODAL WINDOW ---------- */
.modal {
   border-radius: 8px;
   padding: 1rem;
   position: relative;
   background-color: $color-bg;
}

.btn-close {
   --circle-size: 2rem;
   --width-line: 90%; /* толщина circle-size - width-line */
   --height-line: 30%; /* длина circle-size - height-line */
   --transition-btn-close: 0.5s;
   position: absolute;
   width: var(--circle-size);
   height: var(--circle-size);
   background-color: #fff;
   top: 10px;
   right: 20px;
   border-radius: 100%;
   border: none;
   transition: all var(--transition-btn-close) ease-in-out;
   z-index: 100;
   cursor: pointer;

   &:hover {
      background-color: $color-blue-2;
      &::before,
      &::after {
         background-color: #fff;
      }
      &::before {
         transform: translate(-50%, -50%) rotate(-45deg);
      }
      &::after {
         transform: translate(-50%, -50%) rotate(45deg);
      }
   }

   &:active {
      background-color: $color-blue-1;
      transition: none;
      &::before,
      &::after {
         transition: none;
      }
   }

   &::before,
   &::after {
      content: '';
      position: absolute;
      width: calc(var(--circle-size) - var(--width-line));
      height: calc(var(--circle-size) - var(--height-line));
      background-color: $color-black;
      top: 50%;
      left: 50%;
      transition: all var(--transition-btn-close) ease-in-out;
   }

   &::before {
      transform: translate(-50%, -50%) rotate(45deg);
   }
   &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
   }
}

/* Modal Windows */
/* ОБЩИЕ */
.modal {
   padding: 1rem;
   border-radius: 8px;
   background-color: $color-bg;
   position: relative;
   min-width: 400px;

   .modal-wrapper {
      padding: 1rem;
      border-radius: 8px;
      box-shadow: $box-shadow;
   }

   .modal-header {
      margin-bottom: 3rem;

      .btn-close {
         top: 25px;
         right: 25px;
      }
   }

   .modal-content {
      margin-bottom: 2rem;
      text-align: center;
   }

   .modal-buttons {
      display: flex;
      justify-content: center;
      gap: 5rem;

      .btn-modal {
         width: 8rem;
         box-shadow: $box-shadow;
      }
   }

   .modal-icon {
      font-size: 2rem;
      margin: 0 auto;
      display: block;

      &::before {
         content: '';
         position: absolute;
         top: 50%;
         left: 50%;
         width: 3rem;
         height: 3rem;
         border: 2px solid blue;
      }
   }
}

.bottom-buttons {
   margin-bottom: 3rem;
   display: flex;
   gap: 1rem;
}

/* --------- ADD CONTACT ---------- */
.add-contacts {
   min-width: 500px;
   margin: 0 auto;
}

.form-wrap {
   display: flex;
   flex-direction: column;
   max-width: inherit;
   margin: 0 auto;
   box-shadow: $box-shadow;
   padding: 1rem;
   border-radius: 8px;

   .input {
      margin-bottom: 10px;
      padding-left: 10px;
      background: transparent;
      border-radius: 10px;
      box-shadow: $box-shadow-inset;
      width: 100%;

      /* input не нажат */
      .MuiInputLabel-standard {
         transform: translate(10px, 15px) scale(1);
      }

      /* input нажат */
      .MuiInputLabel-shrink {
         transform: translate(10px, 5px) scale(0.75);
      }

      .MuiInputBase-root {
         margin-top: 16px;
      }

      // .css-aqpgxn-MuiFormLabel-root-MuiInputLabel-root,
      // .css-v4u5dn-MuiInputBase-root-MuiInput-root:after,
      // .css-v4u5dn-MuiInputBase-root-MuiInput-root:hover:not(.Mui-disabled, .Mui-error):before,
      // &:hover .css-v4u5dn-MuiInputBase-root-MuiInput-root:hover:not(.Mui-disabled, .Mui-error):before,
      // &:hover .css-v4u5dn-MuiInputBase-root-MuiInput-root:after,
      // .css-v4u5dn-MuiInputBase-root-MuiInput-root:before,
      // &:hover .css-v4u5dn-MuiInputBase-root-MuiInput-root:before {
      // border-bottom: none;
      // }

      // .MuiInputBase-root:hover:not(.Mui-disabled, .Mui-error):before {
      //    border: none !important;
      // }

      .MuiInputBase-root:before {
         border-bottom: none !important;
      }
   }

   .combo-box {
      .MuiInputBase-root {
         padding: 9px !important;
         margin-top: 0 !important;
      }
   }

   .form {
      position: relative;

      .icons {
         position: absolute;
         top: 50%;
         right: 0;
         transform: translate(-50%, -50%);

         svg {
            font-size: 1.5rem;
         }
      }

      /* input combo box */
      .combo-box {
         padding-left: 0;
      }

      // clear btn
      .clear-btn {
         position: absolute;
         padding: 4px;
         top: 50%;
         right: 35px;
         transform: translate(-50%, -50%);

         svg {
            font-weight: 400;
            font-size: 1.25rem;
         }
      }

      &:not(:hover) {
         .clear-btn {
            visibility: hidden;
         }
      }
   }

   // clear button combo box groups
   button[title='Clear'] {
      right: 40px;
   }

   .combo-box input {
      padding: 12px 2px 2px !important;
   }

   .btn-add-contact,
   .btn-edit-contact {
      margin: 30px 0 10px;
      padding: 10px 15px;
      background: transparent;
      border-radius: 8px;
      box-shadow: $box-shadow;

      &:hover {
         background-color: inherit;
      }

      &:active {
         box-shadow: $box-shadow-inset;
      }
   }
}

.btn__add-contact {
   position: fixed !important;
   bottom: 3rem;
   right: 1rem;
   width: 4rem;
   height: 4rem;
   box-shadow: $box-shadow;
   background-color: #fff !important;

   svg {
      width: 3rem;
      height: 3rem;
      fill: $color-blue-2;
   }

   &:hover {
      background-color: #f5f5f5 !important;

      svg {
         fill: $color-blue-1;
      }
   }
}

/* --------- CONTACTS ---------- */
.contacts {
   padding-top: 100px;
   margin-bottom: 60px;

   .header-table {
      position: fixed;
      width: 100%;
      padding: 15px 0;
      top: 75px;
      background-color: $color-bg;
      z-index: 1;
      .container {
         display: flex;
         justify-content: space-between;
         align-items: center;
      }

      .filter {
         box-shadow: $box-shadow-inset;
         border-radius: 10px;
         padding: 0 10px 0 0;
         display: flex;
         align-items: center;
         justify-content: space-between;
         width: 20rem;

         .MuiTextField-root {
            width: inherit;
         }

         .clear-filter-btn {
            padding: 4px;
            svg {
               font-size: 1.25rem;
            }
         }
      }
   }

   table {
      display: grid;
      box-shadow: $box-shadow;
      padding: 2rem;
      margin-top: 4.5rem;
      border-radius: 8px;

      thead {
         position: sticky;
         top: 160px;
         z-index: 1;

         .header-title,
         .header-title span {
            color: $color-blue-1;
            font-weight: 700;
         }

         tr {
            background-color: #e6e9ec;
         }

         td {
            height: 40px;
            display: flex;
            align-items: center;

            .arrow-btn {
               svg {
                  fill: $color-blue-1;
                  font-size: 1.25rem;
               }

               &:hover {
                  background-color: initial;
               }
            }

            .arrow-btn {
               visibility: hidden;
            }

            .arrow-btn.visible-btn {
               visibility: visible;
            }

            &:hover {
               .arrow-btn {
                  visibility: visible;
               }
            }

            // с 2 до 6
            &:nth-child(n + 2):nth-child(-n + 6) {
               cursor: pointer;
            }
         }
      }

      tbody tr:hover {
         background-color: rgba(255, 255, 255, 0.3);
      }

      tr {
         display: flex;
         justify-content: space-between;
         align-items: center;
         gap: 1rem;
         margin-bottom: 1rem;
         padding: 0.25rem 1rem;
         background-color: rgba(0, 0, 0, 0.05);
         border-radius: 8px;
         background: transparent;
         border-radius: 10px;
         box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1), -5px -5px 10px #fff;

         td:first-child {
            width: 40px;
         }

         // с 2 до 6
         td:nth-child(n + 2):nth-child(-n + 6) {
            flex: 1;
         }

         .btn-icon-table {
            width: 40px;
            display: flex;
            justify-content: end;
         }

         .btn-table {
            opacity: 0;
            background-color: rgba(255, 255, 255, 0.3);
            &:hover {
               background-color: rgba(0, 0, 0, 0.1);
            }

            &.edit svg {
               fill: #4caf50;
            }
            &.delete svg {
               fill: #f44336;
            }
         }

         &:hover {
            .btn-table {
               opacity: 1;
            }
         }
      }
   }

   /*--------------------------------------------------*/
}

/* --------- FOOTER ---------- */
.footer {
   position: fixed;
   left: 0;
   width: 100%;
   bottom: 0;
   background-color: #394a56;
   font-family: 'Caveat', cursive;
   color: #d1d9e6;
   font-size: 18px;
   font-weight: 700;
}

/* ---------- THEME ------------ */
// .header.light {
// }

body.dark {
   $theme-dark-text: #f6f5f7;
   $theme-dark-bg: #7c8790;
   $theme-dark-box-shadow: 20px 20px 60px #5c636a, -20px -20px 60px #7c8790;
   $theme-dark-box-shadow-inset: 20px 20px 60px #5c636a inset, -20px -20px 60px #7c8790 inset;

   color: $theme-dark-text;
   background-color: $theme-dark-bg;

   .header {
      background-color: $theme-dark-bg;
      box-shadow: $theme-dark-box-shadow;
   }

   h1 {
      color: $theme-dark-text;
      span::after {
         border: 3px solid $theme-dark-text;
      }
   }

   .swith-toggle-theme .toggle {
      box-shadow: $theme-dark-box-shadow-inset;

      .indicator {
         background: #a2a6aa;
         box-shadow: -8px -4px 8px 0px #575757, 8px 4px 12px 0px #878d99;
      }
   }

   .header-table {
      background-color: $theme-dark-bg;
   }

   .contacts {
      .header-table .filter {
         box-shadow: $theme-dark-box-shadow-inset;
      }

      table {
         box-shadow: $theme-dark-box-shadow;

         tr {
            background-color: $theme-dark-bg;
            box-shadow: $theme-dark-box-shadow;
         }

         tbody {
            tr:hover {
               background-color: #8f9ba5;
            }
         }
      }
   }

   .btn__add-contact {
      box-shadow: $theme-dark-box-shadow;
      background-color: $theme-dark-bg !important;
   }
}

.open-modal {
   padding-right: 17px;
}
